@import url(https://fonts.googleapis.com/css2?family=Kristi&family=Montserrat:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root{
  --primary-color: #72BF44;
}
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.app__container{
    width: 100%;
    max-width: 1380px;
    margin:0 auto;
    position: relative;
}
.nav{
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99999;
}
.nav__container{
    max-width: 1380px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
    padding: 0 20px;
}
.nav__logo{
    width: 180px;
    object-fit: contain;
    padding: 15px 0px;
}
.nav__links > ul{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: row;
}
.nav__links > ul > li{
    list-style: none;
}
.nav__links > ul > li > a{
    text-decoration: none;
    font-weight: 500;
    color: #000;
    margin-right: 40px;
}
.nav__donate{
    margin-left: 0;
    background: var(--primary-color);
    color: #fff !important;
    padding: 10px 14px;
    border-radius: 7px;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    -ms-border-radius: 7px;
    -o-border-radius: 7px;
}
.nav__close{
    display: none;
    cursor: pointer;
}
.nav__open{
    display: none;
    cursor: pointer;
}
/* Responsive */
@media (max-width: 768px){
    .nav__links{
        background: rgba(255, 255, 255, 0.815);
        height: 100%;
        position: fixed;
        width: 80%;
        right: 0;
        top: 0;
        max-width: 430px;
        display: none;
    }
    .nav__links > ul{
        flex-direction: column;
    }
    .nav__links > ul > li{
        margin: 20px 0;
    }
    .nav__links > ul > li > a{
        font-size: 1em;
    }
    .nav__close{
        display: block;
        width: 50px;
        margin: 10px;
    }
    .nav__open{
        display: block;
        width: 50px;
        margin: 10px;
    }
    .nav__active{
        display: block;
    }
}
.header{
    min-height: 100vh;
    position: relative;
}
.header__container{
    max-width: 1380px;
    margin: 0 auto;
    position: relative;
    height: 100vh;
    display: flex;
    align-items: center;
    padding: 0 20px;
}
.header__content{
    max-width: 1000px;
    width: 100%;
}
.header__content > h1{
    font-size: 3.7rem;
    font-weight: 400;
    letter-spacing: 0.4rem;
    color: #000;
    margin-bottom: 1rem;
}
.header__content > p{
    font-size: 1.1rem;
    font-weight: 200;
    letter-spacing: 0.1rem;
    color: #363636;
    margin-bottom: 2rem;
    max-width: 700px;
}
.header__content > a{
    font-size: 1rem;
    font-weight: 500;
    letter-spacing: 0.1rem;
    color: #fff;
    padding: 14px 37px;
    background: var(--primary-color);
    text-decoration: none;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    margin-top: 1rem;
    display: block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

@media (max-width: 768px){
    .header__content > h1{
        font-size: 2.6rem;
    }
    .header__content > p{
        font-size: 1rem;
        letter-spacing: 0.1rem;
    }
}
@media (max-width: 576px){
    .header__content > h1{
        font-size: 1.7rem;
    }
    .header__content > p{
        font-size: 0.85rem;
        letter-spacing: 0.1rem;
    }
}
.dataCard{
    display: flex;
    flex-direction: column;
    width: 30%;
    align-items: center;
    flex-wrap: wrap;
}
.dataCard > h3{
    font-size: 1.4rem;
    font-weight: 700;
    margin-top: 30px;
}
.dataCard > h4{
    margin: 0 auto;
    font-size: 1rem;
    font-weight: 500;
    margin-top: 10px;
    max-width: 260px;
}
.dataCard__image{
    width: 20%;
}
@media (max-width: 768px){
    .dataCard{
        margin: 25px 0;
    }
}
@media (max-width: 576px){
    .dataCard{
        width: 100%;
        margin: 20px 0;
    }
}
.siteData{
    width: 100%;
    padding: 80px 0px;
}
.siteData__datacard_wrapper{
    max-width: 1380px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
@media (max-width: 768px){
    .siteData{
        width: 100%;
        padding: 40px 0px;
    }
}
.infoSection{
    padding: 80px 0px;
    width:100%;
}
.infoSection__wrapper{
    max-width: 1380px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 auto;
    padding: 0 20px;
}
.infoSection__text_conten{
    width: 50%;
}
.infoSection__image_conten{
    width: 35%;
    position: relative;
}
.infoSection__image{
    width: 100%;
    box-shadow: 8px 8px 45px -1px rgba(177, 177, 177, 0.25);
    border-radius: 30px;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    -ms-border-radius: 30px;
    -o-border-radius: 30px;
}
.infoSection__icon{
    position: absolute;
    z-index: 2;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    width: 12%;
}
.infoSection__icon:hover {
    cursor: pointer;
}
.infoSection__text_conten > h1{
    font-size: 2.6rem;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 1.6rem;
    color: #404040;
}
.infoSection__text_conten > span{
    font-family: 'Kristi', cursive;
    font-size: 36px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: -23px;
    display: block;
    background: -webkit-linear-gradient(#71bf44f5, #a7afa100);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.infoSection__text_conten > p{
    font-size: 1rem;
    font-weight: 400;
    margin-bottom: 1rem;
    color: #3D3D3D;
    line-height: 182.6%;
}

@media (max-width: 768px){
    .infoSection{
        padding: 40px 0px;
    }
    .infoSection__text_conten{
        width: 100%;
    }
    .infoSection__image_conten{
        width: 100%;
        margin: 20px 0;
    }
}

.treePlaning{
    text-align: center;
    padding: 90px 20px;
    position: relative;
}
.treePlaning > h1{
    max-width: 900px;
    font-size: 3rem;
    padding-bottom: 20px;
    font-weight: 400;
    color: #404040;
    margin:0 auto;
    z-index: 5;
    position: relative;
}
.treePlaning > p{
    max-width: 700px;
    font-size: 1rem;
    padding: 20px 0;
    font-weight: 500;
    color: #7B7B7B;
    margin:0 auto;
    z-index: 5;
    position: relative;
}
.treePlaning__container{
    max-width: 1100px;
    margin: 40px auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items:center;
    z-index: 5;
    position: relative;
}
.treePlaning__plan{
    width: 200px;
    background: #fff;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    margin: 10px 25px;
    height: 230px;
    padding: 20px;
    
}
.treePlaning__plan > h2{
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 6rem;
    color: var(--primary-color);
}
.treePlaning__plan > h3{
    font-weight: 500;
    font-size: 1.2rem;
    color: var(--primary-color);
    padding-bottom: 10px;
}
.treePlaning__plan > h4{
    font-weight: 400;
    font-size: 1rem;
    color: #8E8E8E;
    padding-top: 16px;
}
.treePlaning__plan_own_choose input{
    border: none;
    outline: none;
    font-size: 6rem;
    text-align: center;
    width: 100%;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 30px;
    color: var(--primary-color);
    -moz-appearance: none;
         appearance: none;
    -webkit-appearance: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
  -moz-appearance: textfield;
}
.treePlaning__backdrop{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0.17) 100%);
}
.treePlaning__btn{
    z-index: 5;
    position: relative;
    font-size: 1rem;
    font-weight: 500;
    letter-spacing: 0.1rem;
    color: #fff;
    padding: 14px 37px;
    background: var(--primary-color);
    text-decoration: none;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    margin: 0 auto;
    margin-top: 4rem;
    display: block;
    width: -moz-fit-content;
    width: -webkit-fit-content;
    width: fit-content;
}
@media (max-width: 768px){
    .treePlaning{
        padding: 30px 20px;
    }
    .treePlaning > h1{
        font-size: 2rem;
    }
    .treePlaning > p{
        font-size: 0.89rem;
    }
}
